import React, { useEffect } from "react";
import { storiesOf } from "@storybook/react";

import { LoaderContainer, loader } from "../components/LoaderContainer";

const stories = storiesOf("Loader Test", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add("Loader", () => {
  useEffect(() => {
    loader.set();
    setTimeout(() => {
      loader.reset();
    }, 3000);
  });
  return <LoaderContainer />;
});
